@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Bungee+Shade&display=swap);
* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Major Mono Display', monospace;
  line-height: 1.5;
}

.app {
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #ffb4a2;
  color: #6d6875;
}

h2 {
  color: #A6767F;
  font-size: 2.25rem;
  font-family: 'Bungee Shade', cursive;
  text-align: center;
  margin: 0;
}

.timer {
  color: #6d6875;
  font-size: 10rem;
  margin-bottom: 30px;
}

.buttons {}

.buttons button {
  outline: none;
  border: none;
  font-size: 24px;
  background: #FFDDC2;
  color: #A6767F;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  padding: 15px 30px;
  cursor: pointer;
  transition: 0.3s ease all;
}

.buttons button:hover {
  background: #FFEDD1;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
}

.buttons button:not(:last-child) {
  margin-right: 10px;
}

.buttons button i {
  margin-left: 15px;
  margin-right: -15px;
}
